.vl {
  border-left: 6px solid black;
  height: 80vh;
  position: absolute;
  left: 50%;
  margin-left: -3px;
  margin-top: 10vh;
  top: 0;
}

.title {
  font-size : 60px;
  font-weight: 900;
}

.button {
  width : 200px;
  height : 50px;
  border : 3px solid black;
  color : white;
  background-color: navy;
  cursor : pointer;
  margin-top : 250px;
}

.button:hover{
  background-color:white;
  color : black;
}

.quiz{
  margin-top : 100;
}

.question{
  margin-top : 50px;
  font-size: 20px;
  font-weight: 400;
  text-align: center;}

.counter{
  font-size: 30px;
  font-weight: 500;
  text-align: center;
}

.input{
  width : 200px;
  height : 30px;
  text-align: center;
  margin-top: 30px;
}

.next {
  margin-top: 50px;
  width : 200px;
  height : 30px;
  border : 1 px solid white;
  background-color: lightgreen;
  color : white;
  cursor: pointer;
}
.retry {
  margin-top: 50px;
  width : 200px;
  height : 45px;
  border : 1 px solid white;
  background-color: red;
  color : white;
  cursor: pointer;
}
.correct{
  margin:5px;
  font-size: 25px;
  color : green;
}

.wrong{
  margin:5px;
  font-size: 25px;
  color : red;
}

.summary{
  margin-top: 50px;
  font-size: 30px;
  font-weight: 500;
  text-align: center;
}

.total {
  margin:5px;
  font-size: 25px;
  color : navy;
}

.score {
  margin-top : 100px;
  color : navy;
  font-size: 25px;
  font-weight: 600;
}

.note{
  font-size : 12px;
}

.summary-correct{
  color :green;
  font-size: 15px
}

.summary-wrong{
  color :red;
  font-size : 15px;
}

.summary-question{
  color : white;
  font-size : 20px;
  margin-top: 30px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.carousel{
  height : 200px;
}